export default {
    namespace: "global",
    state: {
        data: [1, 2, 3]
    },
    subscriptions: {
        setupHistory({ dispatch, history }) {
            history.listen((location) => {
             
                if ((location.pathname !== '/')) {
                    if (!(sessionStorage.getItem('token') === 'lanlan')) {

                        history.push('/')
                    }
                }
            })
        }
    },
    effects: {

    },
    reducers: {

    },
}